<template>
  <div>
    <div class="new"></div>
    <div>
      <new-template @submit="handleSubmit"></new-template>
    </div>
  </div>

</template>

<script>
import axios from 'axios'
import NewTemplate from '../common/NewTem'
export default {
  name: 'PostNew',
  components: {
    NewTemplate
  },
  methods: {
    handleSubmit (data) {
      axios.post('/api/posts', data).then(this.handleDataSucc)
    },
    handleDataSucc (res) {
      res = res.data
      if (res.success) {
        this.$router.push('/admin/posts')
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.new
  font-size: 24px
  text-align: center
  margin: 10px 0 30px
  letter-spacing: 1px
  font-weight: 400
</style>



