<template>
  <div class="input-wrapper">
    <div class="title"></div>
    <div class="error-message" v-for="(error, index) of errors" :key="index">
      {{ error }}
    </div>
    <input type="password" class="input input-password" placeholder="" v-model="newPass">
    <input type="password" class="input input-password" placeholder="" v-model="newRepass">
    <button class="content-submit" @click="handleBtnSubmit"></button>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'UserReset',
  data () {
    return {
      newPass: '',
      newRepass: '',
      errors: []
    }
  },
  methods: {
    validInput () {
      this.errors = []
      if (this.newPass && this.newRepass && this.newPass === this.newRepass) {
        return true
      }

      if (!this.newPass) {
        this.errors.push('')
      }

      if (!this.newRepass) {
        this.errors.push('')
      }

      if (!(this.newPass === this.newRepass)) {
        this.errors.push('')
      }
    },
    handleBtnSubmit () {
      if (this.validInput()) {
        const id = this.$route.params.id
        axios.patch('/api/users/' + id + '/reset', {
          newPass: this.newPass,
          newRepass: this.newRepass
        }).then(this.handleResetSucc)
      }
    },
    handleResetSucc (res) {
      res = res.data
      if (res.success) {
        this.$router.push('/admin/users')
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/variables.styl'
  @import '~styles/mixins.styl'
  .input-wrapper
    width: 300px
    margin: 0 auto
    .title
      font-size: 20px
      color: #666
      text-align: center
      margin-bottom: 20px
    .error-message
      font-size: 12px
      text-align: left
      color: #ff3333
      height: 20px
      line-height: 20px
    .input
      inputStyl()
    .input-password
      width: 100%
    .content-submit
      background: $bgColor
      width: 100%
      padding: 10px
      color: #fff
      &:hover
        cursor: pointer
</style>



